import Vue from 'vue'
import Router from 'vue-router'

import carshow from '@/carshow/Headerb'
import NewPage from '@/carshow/NewPage.vue'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/carshow/',
      name: 'carshow',
      component: carshow
    },{
      path:'/NewPage',
      name: 'NewPage',
      component: NewPage
    }
  ]
})



